


* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  background-color: #e6e6e6;
}

header {
  width: 100%;
  height: 10%;
  padding: 30px 2% 10px;
  background-color: #fff;
  /* background: linear-gradient(rgb(168, 168, 168), rgb(202, 168, 168)); */
  position: fixed;
  top: 0;
  display: flex;
  align-items: center;
  z-index: 10;
}

h1 {
  margin: 0;
  padding: 0;
  font-size: 50px;
}

.title-tozaisen{
  text-align: left;
  margin-bottom: 2px;
}

.title-kenkyu{
  text-align: right;
}

.header_a {
  text-decoration: none;
  color: #4b4b4b;
  font-style:oblique;
  font-size: 40px;
}

.header_b {
  text-decoration: none;
  color: #2d2d2d;
  font-style:oblique;

  /* font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif; */
}

ul {
  list-style: none;
  margin: 0;
  display: flex;
}

li {
  margin: 0 0 0 15px;
  font-size: 20px;
}

nav {
  margin: 0 0 0 auto;
}
